            </div>
            <div class='vertical_card' style='margin-left:5%;margin-right:5%'>
                <div style='padding-top:1vh;padding-bottom:1vh;padding-left:1vh;padding-right:1vh'>
                    <div class='ret_wrapper b4'  @click="select_cat(4)">                       
                        <div class='inner'>
                            <div class='flex' style='position: relative;'>
                                <div style='position: relative;'>
                                    <div class='hexagon b4_first'></div>
                                    <div class='b4_first_char'>T</div>
                                </div>
                                <div class='hex_subtext'>ender</div>
                                <div class='hex_legen'>Over $200,001</div>
                            </div>                         
                        </div>
                    </div> 
                </div>  
