        </div>

        <!--
        <div class="col-12" style='text-align:center;padding-top:7%;padding-bottom:5%'>
            <br/>
            <h1 style='font-size:3em'><u><b>Procurement</b></u></h1><br/><br/>
            <div class='proc_entry_card' @click="select_cat(2)">
                Less than $50,000<br/>
                By Quotation
            </div><br/><br/>
            <div class='proc_entry_card' @click="select_cat(3)">
                $50,001 ~ $200,000<br/>
                By Written Quotation
            </div><br/><br/>
            <div class='proc_entry_card' @click="select_cat(4)">
                Over $200,000<br/>
                By Tender
            </div><br/><br/>
        </div>
        -->
    </div>
    <div v-if="step==1"> <!--Step 1-->
    <div class='inner_card_bd' style='padding-top:1em'>
        <label><b>Step 1.1: Fill In Procurement Info.</b></label><br/><br/>
        <div class="row form-group">
            <div class="col-5"><label class="form-control-label">Committee / Department</label></div>  <!--cause fresh-->
            <div class="col-7">
                <select v-model="dept" class='form-control select' @change="load_info" required>
                    <option v-for="dep in dept_list">{{ dep.department }}</option>
                </select>
            </div>
        </div> 
        <div v-if="sub_dept_list.length>0" class="row form-group">
            <div class="col-5"><label class="form-control-label">Sub-department</label></div>  <!--cause fresh-->
            <div class="col-7">
                <select v-model="sub_dept" class='form-control select'>
                    <option value=''>N/A</option>
                    <option v-for="sdep in sub_dept_list">{{ sdep.subdept }}</option>
                </select>
            </div>
        </div> 
        <div class="row form-group">
