            <div class="col-7">
                <select v-model="asset_cat" class='form-control select' required>
                    <option v-for="asset in asset_list">{{ asset.name }}</option>
                </select>
            </div>
        </div> 
        <div class="row form-group" v-if="cat_lv==2">
            <div class="col-5"><label class="form-control-label">Procurement Display Name</label></div>  <!--cause fresh-->
            <div class="col-7">
                <input type="text" class="form-control" v-model="dp_name" required @change="update_progress(25)"/>
            </div>
        </div>
        </div>
        <!--End of Step 1-->
        <!--Step 1.2-->
        <div class='inner_card_bd' style='background-color:rgb(229,229,229)'>
        <label><b>Step 1.2: Enter Company Info.</b></label><br/><br/>
        <div class="row form-group">
            <div class='col-12' style='display:flex;flex-direction:row;'>
                <div class='col-5' style='padding-left:0'>
                    <div style='display:flex;flex-direction:row'>
