                        <input type="text" class="form-control" placeholder="Enter Company Name" v-model="company_name[0]" @keyup="search_company(0)" @change="update_progress(50);search_company(0)"/>
                    </div>                    
                </div>
                <div class='col-5' v-if="cat_lv==2">
                    <input type="file" class="form-control-button" accept="application/pdf" :id="0" multiple="multiple" @change="addfile_pdf(0)" style="height:100%"/>
                </div>
                <div class='col-5' v-if="cat_lv>2"></div>
                <div class='col-2' style='padding-right:0'>
                    <button class='form-control button' type='button' @click="add_company">&nbsp;&nbsp;More&nbsp;&nbsp;</button>
                </div>
            </div>           
        </div>
        <div class="row form-group" v-if="new_co[0]==true">
