                    <div class='col-10' style='padding-left:1.35em'>
                        <div style='display:flex;flex-direction:row'>
                            <input type="text" class="form-control" placeholder="Enter Company Address" v-model="company_address[0]" required/>
                        </div>
                    </div>                    
