                        <input type="text" class="form-control" placeholder="Contact Number" v-model="company_contact[n]" required/>
                    </div>
                </div>
            </div>
        </div>
        </div>
        <!--End of Step 1.2-->

        <!--item cat2 (5001 ~ 50000-->

        <div v-if="cat_lv==2" class='inner_card_bd'>
            <label><b>Step 1.3: Enter Item Info.</b></label><br/><br/>
