                            <input type="text" class="form-control" placeholder="Supply of ......" v-model="item_name[0]" @change="update_progress(75)"/>
                        </div>
                    </div>
                    <div class='col-5'>
                        <label>Quantity</label>
                        <div style='display:flex;flex-direction:row;height:auto'>
                        <input type="number" class="form-control" v-model="quantity[0]" required @change="update_progress(100)"/>
                        <input type='text' class='form-control' v-model="unit[0]" style='width:7em' placeholder='Enter Unit' required maxlength="5"/>
                        </div>
                    </div>
                    <div class='col-2' style='padding-right:0'>
                        <label>&nbsp;</label>
                        <button class='form-control button'  type='button' @click="add_item">&nbsp;&nbsp;More&nbsp;&nbsp;</button>
                    </div>
                </div>
            </div>
            <div class='col-12' v-for="n in counter_item" style='margin-top:0.25em'>
                <div class="row form-group" style='margin-bottom:0'>
                    <div class='col-12' style='display:flex;flex-direction:row'>
                        <div class='col-5' style='padding-left:0'>
                            <div style='display:flex;flex-direction:row'>
                            <label style='padding-top:0.5em;margin-right:0.5em'>{{n+1}}.</label>
                                <input type="text" class="form-control" placeholder="Supply of ......" v-model="item_name[n]"/>
                            </div>
                        </div>
                        <div class='col-5'>
                            <div style='display:flex;flex-direction:row;height:auto'>
                            <input type="number" class="form-control" v-model="quantity[n]" required/>
                            <input type='text' class='form-control' v-model="unit[n]" style='width:7em' placeholder='Enter Unit' required/>
                            </div>
                        </div>
                        <div v-if="cat_lv==2" class='col-2' style='padding-right:0'>
                            <button class='form-control button'  type='button'  @click="remove_item">Remove</button>
                        </div>
                    </div>
                </div>
            </div>
            </div>    
        </div>
        <!--End of Step 1.2-->
        <!--cat 3 (50,001 ~ 200,000)-->
        <div v-if="(cat_lv==3)||(cat_lv==4)" class='inner_card_bd'>
            <div v-if="(cat_lv==3)"><label><b>Step 1.3: Enter Written Quotation Info.</b></label><br/><br/></div>  
            <div v-if="(cat_lv==4)"><label><b>Step 1.3: Enter Tender Info.</b></label><br/><br/></div> 
            <div>
                <div class="row form-group">
                    <div class='col-12' style='display:flex;flex-direction:row'>
                        <div class='col-5' style='padding-left:0'>
                            <label>Title</label>
                            <input type="text" class="form-control" placeholder="Enter Header of Tender/Written Quotation (e.g. Supply of ... )" v-model="tendor" @change="update_progress(57.5)"/>
                        </div>
                        <div class='col-5'>
                            <label>Fund Source(s)</label>
                            <input type="text" class="form-control" placeholder="Enter Funding Source(s)" v-model="fsrc" @change="update_progress(65)" required/>
                        </div>
                        <div class="col-2">
                            <label>Site Visit</label>
                            <select v-model="site" class="form-control select">
                                <option value='no'>False</option>
                                <option value='yes'>True</option>
                            </select>
                        </div>
                    </div>
                </div>
                    <div class="row form-group" v-if="site=='yes'" style='padding-left:0'>
                        <div class='col-12' style='display:flex;flex-direction:row'>
                            <div class='col-5' style='padding-left:0'>
                                <label>Site Visit Info</label>
                                <input type="date" class="form-control" v-model="v_date" required/>
                            </div>
                            <div class='col-5'>
                                <label>&nbsp;</label>
                                <input type="time" class="form-control" v-model="v_time" required/>
                            </div>
                        <!--
                        <label>Site Visit Info</label> 
                            <div class='col-12' style="display:flex;flex-direction:row;padding-left:0">
                                <div class="col-5" style='padding-left:0'>
                                    <input type="date" class="form-control" v-model="v_date"/>
                                </div>
                                <div class="col-5">
                                    <input type="time" class="form-control" v-model="v_time"/>
                                </div>
                            </div>
                            -->
                        </div>
                    </div>        
                    <div class="row form-group">
                        <div class='col-12' style='display:flex;flex-direction:row'>
                            <div class='col-5' style='padding-left:0'>
                                <label>Contact Person</label>
                                <input type="text" class="form-control" placeholder="Search Contact Person" v-model="cp_name" required @keyup="search_user" @change="update_progress(72.5)"/>
                            </div>
                            <div class='col-5'>
                                <label>Contact Number</label>
                                <input type="text" class="form-control" placeholder="Enter Contact Number" v-model="cp_num" required  @change="update_progress(80)"/>
                            </div>
                        </div>
                    </div>
                    <div class="row form-group">
                        <div class='col-12' style='display:flex;flex-direction:row'>
                            <div class='col-5' style='padding-left:0'>
                                <label>Letter Date</label>
                                <input type="date" class="form-control" v-model="op"  @change="update_progress(85);set_min_close()" required/>
                            </div>
                            <div class='col-5'>
                                <label>Return Deadline</label>
                                <input type="date" class="form-control" v-model="close" :min="min_close" @change="update_progress(90)" required/>
                            </div>
                            <div class='col-2'>
                                <!--
                                <label>Type</label>
                                <select class='form-control select' v-model="ssb">
                                    <option value='Regular'>Non-SSB</option>
                                    <option value='SSB'>SSB</option>
                                </select>
                                -->
                            </div>
                        </div>
                    </div>
                    <div class="row form-group">
                        <div class='col-12' style='display:flex;flex-direction:row'>
                            <div class='col-5' style='padding-left:0'>
                                <label>Item Name</label>
                                <input type="text" class="form-control" placeholder="Supply of ..." v-model="item_name[0]"  @change="update_progress(95)"/>
                            </div>
                            <div class='col-5'>
                                <label>Quantity</label>
                                <div style='display:flex;flex-direction:row'>
                                    <input type="number" class="form-control col-7" v-model="quantity[0]" required @change="update_progress(100)"/>
                                    <input type='text' class='form-control col-5' v-model="unit[0]" placeholder='Enter Unit' required maxlength="5"/>
                                </div>
                            </div>
                            <div class='col-2'>
                                <label>&nbsp;</label>
                                <button class='form-control button'  type='button' @click="add_item">&nbsp;&nbsp;More&nbsp;&nbsp;</button>
                            </div>
                        </div>
                    </div>
        <div class="row form-group" v-for="n in counter_item">
            <div class='col-12' style='display:flex;flex-direction:row'>
                <div class='col-5' style='padding-left:0'>
                    <input type="text" class="form-control" placeholder="Supply of ..." v-model="item_name[n]"/>
                </div>
                <div class='col-5'>
                    <div style='display:flex;flex-direction:row'>
                        <input type="number" class="form-control col-7" v-model="quantity[n]" required @change="update_progress(100)"/>
                        <input type='text' class='form-control col-5' v-model="unit[n]" placeholder='Enter Unit' required maxlength="5"/>
                    </div>
                </div>
                <div v-if="(cat_lv==3)||(cat_lv==4)" class='col-2'>
                    <button class='form-control button'  type='button'  @click="remove_item">Remove</button>
                </div>
            </div>
        </div>
        </div>  
    </div>    
    </div>
    <!--step 2-->
    
    <div v-if="step==2"> 
        <div class='inner_card_bd'>
        <label><b>Step 2: Fill In Quotation Info.</b></label><br/>  
        <!--item-->
        <div class="table-responsive">    
                <table v-for="i in counter_company+1" style='width:100%'  class="table table-striped first-td-padding">
                    <tr style='width:100%'>
                        <th style='color:#ffffff;background-color:#db1900;cursor:pointer;font-size:1.5em;width:40%' @click="change_child_display(i)">
                            {{ company_name[i-1] }}      
                        </th>
                        <th style='color:#ffffff;background-color:#db1900;cursor:pointer;font-size:1.5em' @click="change_child_display(i)">
                            Unit Cost
                        </th>
                        <th style='color:#ffffff;background-color:#db1900;cursor:pointer;font-size:1.5em' @click="change_child_display(i)">
                            Total Cost     
                        </th>                    
                    </tr>
                    <tbody :id="'display'+(i-1)" style="display:'';width:100%" >
                    <tr v-for="j in counter_item+1" >               
                        <td style='font-size:1.5em'>{{ item_name[j-1] }}</td>
                        <td>
                            <input type='number' step="0.001" class='form-control' placeholder="Enter Unit Cost"  onfocus="this.value=''" @change="cal_sum((i-1),(j-1))" v-model="cost_item[(i-1)][(j-1)]"/>                        
                        </td>
                        <td>
                            <input type='number' step="0.001" class='form-control' placeholder="Enter Unit Cost" onfocus="this.value=''"  @change="cal_unit((i-1),(j-1))" :id="'cost_total['+(i-1)+']['+(j-1)+']'" v-model="cost_total[(i-1)][(j-1)]"/>                        
                        </td>                
                    </tr>
                    </tbody>
                </table>                                  
        </div>
    </div>
    </div>
    <!--escape step-->
    </div>
    <div class="card-footer" v-if="(step>0)&&(cat_lv==2)">
            <button type="button" class="btn btn-primary btn-nt" @click.prevent="next_step" v-if="step<2">
              <i class="fa fa-arrow-right"></i> Next Step
            </button>
            <button type="submit" class="btn btn-primary btn-sm" v-if="step==2" :disabled="processing">
              <i class="fa fa-paper-plane"></i> Submit
            </button>
            <button type="button" class="btn btn-danger btn-sm" @click="previous_step">
              <i class="fa fa-undo"></i> Return
            </button>
        </div>
        <div class="card-footer" v-if="(step>0)&&((cat_lv==3)||(cat_lv==4))">
            <button type="submit" class="btn btn-primary btn-sm" :disabled="processing">
              <i class="fa fa-paper-plane"></i> Submit
            </button>
            <button type="button" class="btn btn-danger btn-sm" @click="previous_step">
              <i class="fa fa-undo"></i> Return
            </button>
        </div>
        </form>
        <div class="col-xs-3 col-md-3" style='position:fixed;right:250px;top:250px;box-shadow: #888888;'>
           <v-data-table
                :headers="headers"
                :items="recep_data"
                :search="search"
                class="elevation-1"
                :rows-per-page-items="[10]"
                v-if="hv_record"
                >
            <template v-slot:items="props">
                <tr v-on:click="select_sup(props.item.name)" style='cursor:pointer'>
                    <td>Select</td>
                    <td>{{ props.item.name }}</td>
                </tr>
            <template v-slot:pageText="props">
                Items {{ props.pageStart }} - {{ props.pageStop }} out of {{ props.itemsLength }}
            </template>
            
            </template>
            </v-data-table>  
        </div> 
        <div class="col-xs-3 col-md-3" style='position:fixed;right:250px;top:250px;box-shadow: #888888;'>
           <v-data-table
                :headers="headers"
                :items="user_list"
                :search="search"
                class="elevation-1"
                :rows-per-page-items="[10]"
                v-if="hv_user"
                >
            <template v-slot:items="props">
                <td><a href="" :id= props.item.id v-on:click.prevent="select_u(props.item.name)">Select</a></td>
                <td>{{ props.item.name }}</td>
            <template v-slot:pageText="props">
                Items {{ props.pageStart }} - {{ props.pageStop }} out of {{ props.itemsLength }}
            </template>
            
            </template>
            </v-data-table>  
        </div>
</div>

</template>


<script>
import axios from 'axios';
export default {
    data(){          
        return{
            selected_p:false,
            processing:false,
            sub_dept_list:[],
            sub_dept:'',
            min_close:'',
            ssb:'Regular',
            percentage:0,
            header_text:'Select Procurement Category',
            dp_name:'',
            user_list:[],
            hv_user:false,
            v_date:'',
            v_time:'',
            cp_name:'',
            cp_num:'',
            site:'no',
            tendor:'',
            fsrc:'',
            op:'',
            close:'',
            sup_list:[],
            user_check:'',
            cat_list:['5001 - 50,000','50,001 - 200,000','>200,000'],
            category:'5001 - 50,000',
            counter_company:1,
            counter_item:0,
            cat_lv:0,
            company_name:[],
            company_address:[],
            company_contact:[],
            cost_total:[],
            cost_item:[],
            unit:[],
            list_item:[],
            tmp:0,
            count_total:0,
            item_name:[],
            item:[],
            ready:false,
            dept_list:[],
            display_child:[true],
            quantity:[],
            step:0,
            pdf:[], 
            searching:0,    
            recep_data:[],       
            check_company:false,
            check_item:false,
            check_quant:false,
            hv_record:false,
            asset_list:[],
            asset_cat:'',
            new_co:[],
            headers: [
            { text: 'Action',value :'action',sortable: false},
            { text: 'Name',value:'name',sortable: false},
            ], 
        }  
    },
async created(){
    await axios.get('Procurement/department/backend/dept_member.php')
        .then(
            (response)=>{
                this.dept_list = response.data;
                this.dept = this.dept_list[0].department;
                this.load_subdept();
            }
        ); 
    await axios.get('Procurement/shared_backend/check_user_setup.php')
        .then(
            (response)=>{
                this.user_check = response.data[0].signed;
                var perm = response.data[0];
                if(!(this.user_check=='set')){
                    this.$router.push('../user_setup');
                }
                if(perm.proc_level<1){
                    this.$alert("No permission").then(
                        ()=>{
                            this.$router.go(-1);
                        }
                    )
                }
            }
        )
    var formData = new FormData();
    formData.append('dept',this.dept);
    await axios.post('Procurement/budget/backend/asset.php',formData)
            .then(
                (response)=>{
                    this.asset_list = response.data;
                    this.asset_cat = this.asset_list[0].name;
                }
            )
  },
  
async mounted(){
    window.removeEventListener('focus', console.log('Lost Focus'));
    //asset category of department   

},   
    methods:{
        update_progress(percentage){
            if(this.percentage<percentage){
                this.percentage = percentage;
            }
        },
        select_u(name){
            this.cp_name = name;
            this.hv_user = false;
            this.selected_p = true;
        },
        search_user(){
            this.selected_p = false;
            var key = this.cp_name;
            if(key==''){
                this.hv_user=false;
            } else {
                var formData = new FormData();
                formData.append('key',key);
                axios.post('Procurement/procurement/backend/search_user.php',formData)
                    .then(
                        (response)=>{
                            if(!(response.data=='Empty')){
                                this.hv_user = true;
                                this.user_list = response.data;
                            } else {
                                this.user_list = '';
                                this.hv_user = false;
                            }
                        }
                    )
            }
        },
        load_subdept(){
            var formData = new FormData();
            formData.append('dept',this.dept);
            axios.post('Procurement/shared_backend/subdept.php',formData)
            .then(
                (response)=>{
                    if(response.data!='empty'){
                        this.sub_dept_list = response.data;
                        this.sub_dept = '';
                    } else {
                        this.sub_dept_list=[];
                        this.sub_dept = '';
                    }
                }
            )
        },
        load_info(){
            var formData = new FormData();
            formData.append('dept',this.dept);
            axios.post('Procurement/budget/backend/asset.php',formData)
            .then(
                (response)=>{
                    this.load_subdept();
                    this.asset_list = response.data;
                    this.asset_cat = this.asset_list[0].name;
                }
            )
        },
        addfile_pdf(event){
        var n = event;
             this.pdf[n] = document.getElementById(n).files;         
        }, 
        add_company(){
            this.counter_company = this.counter_company+1;
        },
        remove_company(){
            this.counter_company = this.counter_company-1;
        },
        add_item(){
            this.counter_item = this.counter_item+1;
        },
        remove_item(){
            this.counter_item = this.counter_item-1;
        },
        next_step(){
            var i = 0;
            var j = 0;
            var x = 0;
            var y = 0;
            var n=0;
            this.check_company = false;
            this.check_item = false;
            for(i=0;i<this.counter_company+1;i++){
                  if(this.new_co[i]==true){
                    if((this.company_name[i]==undefined)||(this.company_address[i]==undefined)||(this.company_address[i]==undefined)){
                        this.check_company = false;
                    } else if((!(this.company_name[i].length>0)||!(this.company_address[i].length>0)||!(this.company_address[i].length>0))){
                        this.check_company = false;
                    } else if((this.company_name[i].length>0)&&(this.company_address[i].length>0)&&(this.company_address[i].length>0)){
                        this.check_company = true;
                    }
                } else {
                    if(this.company_name[i]==undefined){
                        this.check_company = false;
                    } else if((this.step==1)&&!(this.company_name[i].length>0)){
                        this.check_company = false;
                    } else if((this.step==1)&&(this.company_name[i].length>0)){
                        this.check_company = true;
                    }  
                }
            };  
            for(j=0;j<this.counter_item+1;j++){
                    if((this.item_name[j]==undefined)||(this.quantity[j]==undefined)){
                    this.check_item = false;
                } else if((this.step==1)&&!(this.item_name[j].length>0)&&!(this.quantity[j]>0)){
                    this.check_item = false;
                } else if((this.step==1)&&(this.item_name[j].length>0)&&(this.quantity[j]>0)){
                    this.check_item = true;
                }    
            }  
            if((this.check_company==true)&&(this.check_item==true)){
                for(x=0;x<this.counter_company+1;x++){
                    this.cost_total[x] = [];
                    this.cost_item[x] = [];
                    for(y=0;y<this.counter_item+1;y++){
                        this.cost_total[x][y] = 0;
                        this.cost_item[x][y] = 0;
                    }
                }
                this.step = this.step+1;
            } else if((this.check_company==false)){
                this.$alert('Please Enter Company Info.')
            } else if((this.check_item==false)){
                this.$alert('Please Enter Item Info.');
            }
        },
        previous_step(){
            if(!(this.step==0)){
                if(this.step==1){
                    this.header_text = 'Select Procurement Category';
                }
            this.step = this.step-1;
            } else {
                this.$router.go(-1);
            };
        },
        change_child_display(id){
        var display_id = 'display'+(id-1);
            if(document.getElementById(display_id).style.display!='none'){
                document.getElementById(display_id).style.display='none';
            } else if(document.getElementById(display_id).style.display=='none'){
                document.getElementById(display_id).style.display='';
            }
        },
        cal_sum(x,y){   
            var id = 'cost_total['+x+']['+y+']';
            this.cost_item[x][y] = Number(this.cost_item[x][y]);            
            var tmp = Number(this.cost_item[x][y]) * Number(this.quantity[y]);      
            this.cost_total[x][y] = Number(tmp);
            document.getElementById(id).value=this.cost_total[x][y]
        },
        cal_unit(x,y){
            var id = 'cost_total['+x+']['+y+']';
            var tmp = document.getElementById(id).value;
            var result = Number(tmp)/Number(this.quantity[y]);
            result = Math.round(result * 100) / 100;
            this.cost_item[x][y] =  result;
            this.$forceUpdate();
            console.log(result);
        },
        update_cat(event){
            this.percentage = 0;
            if(this.category=='5001 - 50,000'){               
                this.cat_lv=2;
                this.counter_company=1;
            } else if(this.category=='50,001 - 200,000'){               
                this.cat_lv=3;
                this.counter_company=4;
            } else if(this.category=='>200,000'){       
                this.cat_lv=4;
            }
        },
        search_company(event){
            this.searching = event;
            var formData = new FormData();
            formData.append('key',this.company_name[event]);
            axios.post('Procurement/shared_backend/supplier_list.php',formData)
            .then(
                (response)=>{
                    if(response.data == "Empty"){
                       this.hv_record=false; 
                        this.new_co[event]=false;
                    } else if(response.data == 'new'){
                        this.new_co[event]=true;
                        this.hv_record=false;
                    } else {
                        this.new_co[event]=false;
                        this.recep_data = response.data;
                        this.hv_record=true;
                    }
                }); 
        }, 
        select_sup(event){
            var id = this.searching;
            var i = 0;
            var sel_id = 0;
            var selected = false;
            for(i=0;i<this.counter_company;i++){
                if(this.company_name[i]==event){
                    selected = true;  
                    sel_id = i+1;                 
                }
            }   
            if(selected==true){
                this.$alert(event+' has already be selected as company:'+sel_id+' !');               
            } else {
                this.company_name[id] = event;
                this.hv_record=false;
            }    
        },
        select_cat(cat){
            if(cat==2){
                this.header_text = 'Procurement Form : < $50,000';
                this.cat_lv=2;
                this.counter_company=1;
                this.$forceUpdate();
            } else if(cat==3){
                this.header_text = 'Procurement Form : $50,001 ~ $200,000';
                this.cat_lv=3;
                this.counter_company=4;
                this.$forceUpdate();
            } else if(cat==4){
                this.header_text = 'Procurement Form : > $200,000';
                this.cat_lv=4;
                this.counter_company=4;
                this.$forceUpdate();
            }
            this.step = 1;
        },
        set_min_close(){
            var st = new Date(this.op); 
            st = st.getTime();
            var st_new = new Date(st+(21*24*60*60*1000));
            var date = st_new;
            var monthNames = [
                "January", "February", "March",
                "April", "May", "June", "July",
                "August", "September", "October",
                "November", "December"
            ];
            var day = date.getDate();
            var monthIndex = date.getMonth();
            var year = date.getFullYear();
            if(parseInt(monthIndex)<9){
                monthIndex = '0'+parseInt(monthIndex+1);
            } else {
                monthIndex = parseInt(monthIndex+1);
            } 
            if(parseInt(day)<9){
                day = '0'+parseInt(monthIndex);
            } else {
                day = parseInt(day);
            } 
            st_new = year + '-' + monthIndex + '-' + day;
            this.min_close = st_new;
        },
        async handleSubmit(){
            this.processing = true;
            //
            if(this.cat_lv==2){
            var i = 0;
            var j = 0;
            var p = 0;
            var bi = 0;
            var formData = new FormData();
            var count_files = 0;
            if(this.sub_dept!=''){
                formData.append('subdept',this.sub_dept);
            }
            formData.append('display',this.dp_name);
            formData.append('dept',this.dept);
            formData.append('asset',this.asset_cat);
            formData.append('category',this.cat_lv);
            formData.append('counter_company',this.counter_company);
            formData.append('counter_item',this.counter_item);
            for(bi=0;bi<this.counter_item+1;bi++){
                formData.append('item'+bi,this.item_name[bi]);
                formData.append('quantity'+bi,this.quantity[bi]);
                formData.append('unit'+bi,this.unit[bi]);
            }
            for(i=0;i<this.counter_company+1;i++){
                count_files = 0;
                formData.append('company_'+i,this.company_name[i]);  
                if(this.new_co[i]==true){
                    formData.append('new_sup_'+i,this.new_co[i]);
                    formData.append('company_address_'+i,this.company_address[i]); 
                    formData.append('company_contact_'+i,this.company_contact[i]); 
                }              
                for(j=0;j<this.counter_item+1;j++){
                    formData.append('com_'+i+'_item_'+j,this.item_name[j]);   
                    formData.append('com_'+i+'_ucost_'+j,this.cost_item[i][j]);      
                    formData.append('com_'+i+'_totalcost_'+j,this.cost_total[i][j]);               
                }
                if(this.pdf[i]!=undefined){
                    for(p=0;p<this.pdf[i].length;p++){
                        let file = this.pdf[i][p];
                        formData.append('files'+i+'_'+p, file);  
                        count_files = count_files+1;
                    }
                }
                formData.append('count_file'+i, count_files);
                
            }
            await axios.post('Procurement/procurement/backend/insert_proc.php',formData)
            .then(
                (response)=>{
                    this.$alert('Successfully Submitted !').then(
                        ()=>{
                            this.processing = false;
                        }
                    )
                    this.$router.push('view_proc');
                }
            );
        } else if(this.cat_lv>=3){
            if(this.selected_p){
                var i = 0;
                var j = 0;
                var p = 0;
                var bi = 0;
                var formData = new FormData();
                var count_files = 0;
                formData.append('ssb',this.ssb);
                formData.append('display',this.tendor);
                formData.append('op',this.op);
                formData.append('v_date',this.v_date);
                formData.append('v_time',this.v_time);
                formData.append('cp_name',this.cp_name);
                formData.append('cp_num',this.cp_num);
                formData.append('fund_src',this.fsrc);
                formData.append('close',this.close);
                formData.append('dept',this.dept);
                formData.append('title',this.tendor);
                formData.append('asset',this.asset_cat);
                formData.append('category',this.cat_lv);
                formData.append('counter_company',this.counter_company);
                formData.append('counter_item',this.counter_item);
                for(bi=0;bi<this.counter_item+1;bi++){
                    formData.append('item'+bi,this.item_name[bi]);
                    formData.append('quantity'+bi,this.quantity[bi]);
                    formData.append('unit'+bi,this.unit[bi]);
                }
                for(i=0;i<this.counter_company+1;i++){
                    count_files = 0;
                    formData.append('company_'+i,this.company_name[i]);  
                    if(this.new_co[i]==true){
                        formData.append('new_sup_'+i,this.new_co[i]);
                        formData.append('company_address_'+i,this.company_address[i]); 
                        formData.append('company_contact_'+i,this.company_contact[i]); 
                    }              
                    if(this.pdf[i]!=undefined){
                        for(p=0;p<this.pdf[i].length;p++){
                            let file = this.pdf[i][p];
                            formData.append('files'+i+'_'+p, file);  
                            count_files = count_files+1;
                        }
                    }
                    formData.append('count_file'+i, count_files);
                    
                }
                await axios.post('Procurement/procurement/backend/insert_proc_cat3.php',formData)
                .then(
                    (response)=>{
                        this.$alert('Successfully Submitted !').then(
                            ()=>{
                                this.processing = false;                      
                            }
                        )  
                        this.$router.push('view_proc');                 
                    }
                );
            } else {
                this.$alert('Please select contact person').then(
                    ()=>{
                        this.cp_name = '';
                        this.processing = false;
                        this.selected_p = false;
                    }
                )
            }
        }
        }
        //
        }
}
</script>


<style scoped>
  .input-group-addon {
    background-color: transparent;
    border-left: 0;
}
input[type=date]::-webkit-inner-spin-button {
    -webkit-appearance: none;
    display: none;
}
</style>


